body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0px;
    padding: 0px;
}

.ctnr{
    background-color: rgb(238, 238, 238);
    width: 100%;
    display: grid;
    grid-template-columns: 10vw auto auto auto;
    grid-column-gap: 0px;
    -webkit-column-gap: 0px;
            column-gap: 0px;

}

.sidenav{
    width: 10vw;
    height: 100vh;
    background-color: rgb(18, 18, 18);
    grid-row: 1/7;
    grid-column: 1/2;
    
    
}

.mynav{
    grid-column: 2/6;
    grid-row: 1/2;
    width: 100%;
    height: 10vh;
    background-color: rgb(191, 191, 193);
    position: -webkit-sticky;
    position: sticky;
}

.top{
    grid-column: 2/6;
    grid-row: 2/3;
    background-color: rgb(236, 240, 236);
    width: 100%;
    height: 15vh;
}

.crd-container{
    grid-column: 2/5;
    grid-row: 3/5;
    
}


/* modal */

.add-status{
    display: flex;
    padding-left: 0.5rem;
    padding-top: 0.5rem;
}
.add-status div{
    margin-left: 1rem;
}

.date{
    display: grid;
    padding-top: 1rem;
}
.date input{
    width: 13vw;
    height: 5vh;
    margin-top: 1rem;
    margin-left: 1rem;
    border-radius: 5px;
    border: 1px solid rgb(112, 110, 110);

}



.mynav{
    display: grid;
    grid-template-columns: auto auto;
    padding: 0px;
    justify-content: left;
}

.first-container{
    grid-column: 1/2;
    display: grid;
    grid-template-columns: auto auto;
    padding: 1rem;
    justify-content: center;
    align-items: center;
    width: 60vw;
    
    
}

.create-task-btn{
    grid-column: 1/2;
    width: 14vw;
    position: absolute;
    left: 3rem;
    border-right: 1px solid rgb(92, 91, 91);
    height: 6vh;
    padding-top: 0.1rem;
}

.create-task-btn a{
    text-decoration: none;
    color: white;
    background-color: rgb(62, 126, 244);
    padding: 0.5rem 3rem 0.5rem 3rem;
    border-radius: 2rem;
    
}

.srch{
    grid-column: 2/3;
    position: relative;
    
}

.srch input{
    border: 1px solid rgb(255, 253, 253);
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    width: 15vw;
    height: 4.5vh;
    padding-left: 2rem;
}

.srch button{
    width: 3vw;
    height: 4.5vh;
    text-align: center;
    border: none;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    background-color: rgb(62, 126, 244);
    color: white;
}

.srch input:focus{
    border: 1px solid white;
}
.sidenav {
    list-style: none;
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 0px;
    margin: 0px;
    width: 100%;

    grid-row-gap: 0px;

    row-gap: 0px;
}



.sidenav ul div {
    width: 100%;
    padding: 1rem;
    grid-column: 1/4;
    grid-row: 1/2;
    background-color: rgb(18, 18, 18);
    height: 20vh;
}

.sidenav ul div img {

    width: 100%;
    height: 100%;
    padding: 0.5rem;
}

.sidenav ul li {
    list-style: none;
    padding: 1.3rem;
    margin-top: -2rem;
    width: 10vw;
    height: 15vh;
    border-bottom: 1px solid rgb(160, 158, 158);
    text-align: center;
    background-color: rgb(18, 18, 18);
}

.sidenav ul li:hover {
    background-color: rgb(138, 126, 45);
}

.sidenav-item1 {
    grid-column: 1/4;
    grid-row: 2/2;
}

.sidenav-item2 {
    grid-column: 1/4;
    grid-row: 3/4;

}

.sidenav-item3 {
    grid-column: 1/4;
    grid-row: 4/5;

}

.sidenav-item4 {
    grid-column: 1/4;
    grid-row: 5/6;

}

.sidenav-item5 {
    grid-column: 1/4;
    grid-row: 6/7;

}

.sidenav ul li .img {
    width: 2rem;
    height: 2rem;

    border: 1px solid rgb(172, 171, 171);
    border-radius: 50%;
    border: 3px solid rgb(218, 218, 218);
    display: flex;
    margin-right: auto;
    margin-left: auto;
}

.sidenav ul li .img:hover {
    background-color: rgb(138, 126, 45);
}

.sidenav ul li a {
    text-decoration: none;
    color: white;
}

.top{
    display: grid;
    grid-template-columns: auto auto;
    
    
}

.top-img{
    border-right: 1px solid black;
    position: relative;
    justify-self: left;
    align-self: center;
    width: 15vw;
    height: 15vh;
    margin-top: 1rem;
    margin-left: -8rem;
    
}

.top-img img{
    width: 85%;
    height: 100%;
    
}

.top-heading{
    justify-self: left;
    align-self: center;
    margin-left: -28rem;
   
}

/* .top h1{
    color: rgb(143, 140, 140);
    text-align: center;
    align-items: center;
    margin-top: 0.8em;
    margin-bottom: auto;
} */

.crd-container{
    display: grid;
    grid-template-columns: auto auto auto;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    padding: 3rem;
    
}

.crd{
    width: 26vw;
    height: 75vh;
    background-color: rgb(226, 226, 226);
    border-radius: 10px;
    box-shadow: 8px 8px 8px rgb(158, 157, 157);
    overflow: scroll;
    
}::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}

.card1{
    grid-column: 1/2;
}

.card2{
    grid-column: 2/3;
}

.card3{
    grid-column: 3/3;
}

.crd-item{
    padding: 1rem;
}

.crd-item h2{
    padding: 1rem;
}
.crd-item a{
    text-decoration: none;
}
.crd-task{
    padding: 1rem;
    margin-top: 0.5rem;
    background-color: rgb(254, 237, 49);
    color: white;
    border-radius: 15px;
    color: black;
    width: 24vw;
}


.crd-task p span{
    font-weight: bold;
    color: black;
}


@media (max-width:700px) {

    .crd{
        width: 70vw;
        height: 65vh;
        background-color: rgb(226, 226, 226);
        border-radius: 10px;
        box-shadow: 8px 8px 8px rgb(158, 157, 157);
        margin-top: 2rem;
        
    }
    .card1{
        grid-column: 1/3;
        grid-row: 1/2;
    }
    
    .card2{
        grid-column: 1/3;
        grid-row: 2/3;
    }
    
    .card3{
        grid-column: 1/3;
        grid-row: 3/4;
    }
    
}


